@import "../../global.scss";

.skills {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;

  @include mobile{
    justify-content: space-around;
  }


  h1 {
    font-size: 50px;

    @include mobile{
      font-size: 20px;
    }
  }

  p{
    font-size: 22px;
    
    @include mobile{
      font-size: 15px;
    }  
  }
  ul {
    -moz-column-count: 4;
    -moz-column-gap: 20px;
    -webkit-column-count: 4;
    -webkit-column-gap: 20px;
    column-count: 2;
    column-gap: 20px;

    @include mobile{
        column-count: 1;
      }
  }

  .container {
    width: 80%;
    
    align-items: center;
    justify-content: center;

     @include mobile{
       flex-direction: column;
       height: 100%;
     }
    }
  }

  .skilsset{
      text-align: left;
      font-weight: 700;
      
  }

@import url(https://fonts.googleapis.com/css?family=PT+Sans);

$font-family: 'PT Sans', sans-serif;

$li-padding: 5px;

// colors
$background-color: #1a1a1a;
$progressbar-title-color: #fff;

$red: #c0392b;
$blue: #0579e4;
$purple: #9b59b6;
$orange: #ffa500;
$green: #27ae60;

$animation-delay: .7s;

@mixin border-radius($radius){
  -webkit-border-radius: $radius;
     -moz-border-radius: $radius;
      -ms-border-radius: $radius;
          border-radius: $radius;
}

*,
*:before,
*:after{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,body{
  background: $background-color;
  width: 100%;
  height: 100%;
  font-family: $font-family;
}

.skills-bar-container{
  list-style:none;
  
  

  li{
    position: relative;
    width: 100%;
    padding: $li-padding;
    margin-bottom: 10px;
    
    .progressbar-title{
      color: $progressbar-title-color;
      color: black;
      h3{
        display: inline-block;
      }
      .percent{
        position: absolute;
        color: black;
        right: $li-padding;
      }
    }// end progressbar-title
    
    .bar-container{
      background: #fd8989;
      position: relative;
      width: 100%;
      height: 10px;
      margin-top: 5px;
      display: block;
      @include border-radius(5px);
      
      .progressbar{
        position:absolute;
        width:0%;
        height: 100%;
        @include border-radius(5px);

        -webkit-animation-duration: 2s;
        animation-duration: 2s;
        -webkit-animation-timing-function: ease-out;
        animation-timing-function: ease-out;
        -webkit-animation-fill-mode: both;
        animation-fill-mode: both;
      }

      #progress-css{
        -webkit-animation-name: progress-css;
        animation-name: progress-css;
        -webkit-animation-delay: $animation-delay*2;
        animation-delay: $animation-delay*2;
      }
    }// end bar-container
  }
}

.progressblue{background-color:$blue;}
@-webkit-keyframes progress-css{0%{width: 0%;} 100%{ width: var(--to-width);}}
@-moz-keyframes progress-css{0%{width: 0%;} 100%{ width: var(--to-width);}}
@keyframes progress-css{0%{width: 0%;} 100%{ width: var(--to-width);}}